.container {
    position: fixed;
    top:0px;
    left: 0px;
    right: 0px;
    padding: 0px 25px;
    background-color: var(--color-blue-700);
    height: 50px;
    display: flex;
    align-items: center;
    gap: 20px;
}

.title {
    font-weight: bold;
    font-size: 22px;
    color: white;
}

.menuIcon {
    cursor: pointer;
    font-size: 24px;
    color: white;
}

