.container{
    background-color: white;
    max-width: 700px;
    border: 2px solid var(--color-gray-300);
    border-radius: 4px;
    margin: 50px auto;
    padding: 25px;
}

.title{
    color: var(--color-blue-600);
    font-weight: bold;
    font-size: 24px;
    margin: 0px auto 30px;
    text-align: center;
}

.group{
    margin-bottom: 30px;
}

.label{
    font-weight: 600;
    margin-bottom: 10px;
    display: block;
}

.input{
    width: 100%;
    border: 2px solid var(--color-gray-300);
    padding: 8px 10px;
    border-radius: 4px;
    font-size: 16px;
    outline: none;
}

.input:focus{
    border-color: var(--color-blue-600);
}

.inputError{
    border-color: var(--color-red-600);
}

.inputError:focus{
    border-color: var(--color-red-600);
}

.error{
    color: var(--color-red-600);
    margin-top: 4px;
}

@media (max-width: 900px) {
    .container{
        margin: 10px;
        padding: 15px;
    }
}