.closeIcon{
    position: absolute;
    right: 5px;
    top: 5px;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    border-radius: 50%;
    padding: 2px;
    cursor: pointer;
}

.imageContainer{
    position: relative;
    display: inline-block;
    margin-top: 10px;
}

.imagePreview{
    width: 200px;
    margin: auto;
    display: block;
    object-fit: cover;
    border-radius: 4px;
}
