.container {
    width: 240px;
    background-color: var(--color-blue-600);
    height: 100vh;
    position: fixed;
    top: 0px;
    overflow-y: auto;
    transition: all;
    transition-duration: 300ms;
    margin-top: 50px;
    z-index: 99;
}

.opened {
    left: 0px;
}

.closed {
    left: -240px;
}

.menuItem {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 16px;
    border-left: 6px solid transparent;
    cursor: pointer;
}

.menuItemLabel {
    font-size: 16px;
    color: white;
}

.menuItemIcon {
    color: white;
}

.activeMenuItem,
.menuItem:hover {
    background-color: var(--color-blue-500);
    border-left-color: var(--color-blue-700);
}
