.container {
    background-color: white;
    border: 2px solid var(--color-gray-300);
    border-radius: 4px;
    overflow: hidden;
    padding: 25px;
    margin: 50px;
    position: relative;
}

.wrapper {
    overflow-x: auto;
    width: 100%;
}

.table{
    width: 100%;
    min-width: 700px;
    background-color: white;
    border-collapse: collapse;
    text-align: center;
}

.table tr:nth-child(even), 
.table tr:hover,
.table th{
    background-color: var(--color-gray-100);
}

.table th{
    position: relative;
    cursor: pointer;
}

.sortIcon{
    position: absolute;
}
 
.table th,
.table td{
    border: 2px solid var(--color-gray-300);
    padding: 10px;
}

.img{
    width: 60px;
    height: 60px;
    object-fit: cover;
    border-radius: 4px;
    margin: auto;
}

.btnDanger,
.btnWarning{
    padding: 6px;
    border-radius: 4px;
    color: white;
    cursor: pointer; 
}

.btnWarning {
    background-color: var(--color-amber-500);
}

.btnDanger {
    background-color: var(--color-red-600);
    margin-left: 5px;
}

.searchContainer {
    text-align: right;
    margin-bottom: 20px;
}

.searchInput{
    border: 2px solid var(--color-gray-300);
    padding: 8px;
    border-radius: 4px;
    outline: none;
}

.searchInput:focus {
    border-color: var(--color-blue-600);
}

.paginationContainer {
    display: flex;
    align-items: center;
    justify-content: right;
    gap: 10px;
    margin-top: 20px;
}

.paginationText{
    color: var(--color-gray-600);
}

.btnPagination{
    border: 2px solid var(--color-gray-300);
    background-color: var(--color-gray-200);
    border-radius: 4px;
    padding: 6px;
    cursor: pointer;
}

.loaderOverlay{
    position: absolute;
    background-color: rgba(0,0,0,0.2);
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loaderContainer{
    background-color: white;
    border-radius: 5;
    padding: 20px;
}

.loader{
    width: 40px;
    height: 40px;
    border: 4px solid transparent;
    border-left: 4px solid black;
    border-top: 4px solid black;
    border-right: 4px solid black;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@media (max-width: 900px) {
    .container{
        margin: 10px;
        padding: 15px;
    }
}